import { ActualitesOfflinePage } from './../actualites-offline/actualites-offline';

import { Component } from '@angular/core';
import { Http, Headers } from '@angular/http';
import { NavController } from 'ionic-angular';

import { InscriptionPage } from './../inscription/inscription';
import { NotificationsPage } from './../notifications/notifications';

import { ArticlesUtilisateur } from '../../providers/articles-utilisateur';


@Component({
  selector: 'page-connexion',
  templateUrl: 'connexion.html'
})
export class ConnexionPage {

  username: string = '537639-L';
  password: string = '123456' ;
 
  constructor(public nav: NavController, public http: Http, public artUsers: ArticlesUtilisateur) {
 
  }
 
  connexion(){
 
      let headers = new Headers();
      headers.append('Content-Type', 'application/json');
 
      let credentials = {
        username: this.username.toLowerCase(),
        password: this.password
      };
 
      this.http.post('http://localhost:3000/auth/login', JSON.stringify(credentials), {headers: headers})
        .subscribe(res => {
          //console.log(res.json());
          this.artUsers.init(res.json());
          this.nav.setRoot(NotificationsPage);
        }, (err) => {
          console.log(err);
        });
 
  }
 
  inscription(){
    this.nav.push(InscriptionPage);
  }

  skip() {
    this.nav.push(ActualitesOfflinePage,{authentifie: false});
  }

}





