
import { Component } from '@angular/core';
import { NavController, NavParams, ModalController, ToastController } from 'ionic-angular';

import { Articles } from './../../providers/articles';
import { ArticlesUtilisateur } from './../../providers/articles-utilisateur';
import { ArticleComponent } from './../../components/article/article';


@Component({
  selector: 'page-actualites-offline',
  templateUrl: 'actualites-offline.html'
})
export class ActualitesOfflinePage {
actualite;
affiche;
articles;
articlesRecents;
recherche;
articlesFiltres;
longueur;
messageNombreRecherche

  constructor(private toastCtrl: ToastController, public navCtrl: NavController, public navParams: NavParams, public modalCtrl: ModalController, public  article: Articles, public artUser: ArticlesUtilisateur) {
    this.actualite = "recent";
    this.recherche = "";

  }

  ionViewDidLoad() {

    this.rangerArticles()

    console.log('ionViewDidLoad ActualitesPage');

  }

  presentToast() {
    if (this.longueur >1 ) {
      if (this.recherche) {
        this.messageNombreRecherche = this.longueur + ' articles correspondent à votre recherche.'
      } else this.messageNombreRecherche = "il y'a " + this.longueur + ' articles.'
    } else {
      if (this.recherche) {
        this.messageNombreRecherche = this.longueur + ' article correspond à votre recherche.'
      } else this.messageNombreRecherche = "il y'a " + this.longueur + ' article.'
    }

  let toast = this.toastCtrl.create({
    message: this.messageNombreRecherche,
    duration: 3000,
    position: 'bottom'
  });

  toast.present()

  }


  afficheBarreRecherche(){
    if (this.affiche){
      this.affiche = false;
    }
    else this.affiche = true;
  }

  ouvrirArticle(article){
    let modal = this.modalCtrl.create(ArticleComponent, {
      article: article
    })
    modal.present();
  } 

  filterArticles (data, searchTerm) {
        return data.filter((item) => {
            return item.auteur.toLowerCase().indexOf(searchTerm.toLowerCase()) > -1;
        });     
    }

  rangerArticles () {
    this.article.obtenirArticle().then((data) => {

      this.articles = data;
      this.articlesRecents = this.articles.sort(
        function(a,b){
          a= new Date(a.datePost);
          b= new Date(b.datePost);
          return  a>b ? -1 : a<b ? 1 : 0;
        }
      )

      this.articlesFiltres = this.articlesRecents.filter((item) => {
        var j=0;
        for(var i = 0; i < item.tags.length; i++) {
            if(item.tags[i].indexOf(this.recherche.toLowerCase()) > -1) {
                j= j+1
            }
        }
            return (
                item.auteur.toLowerCase().indexOf(this.recherche.toLowerCase()) > -1 ||
                item.titre.toLowerCase().indexOf(this.recherche.toLowerCase()) > -1 ||
                j > 0
              );
        }); 

        this.longueur = this.articlesFiltres.length
      
    });
    
  }

}


