import { Injectable, Pipe, PipeTransform } from '@angular/core';
import * as moment from 'moment'
import 'moment/locale/fr';

@Pipe({
  name: 'moment',
  pure: false,
})
@Injectable()
export class Moment implements PipeTransform {
  /*
    
   */
  transform(value:Date | moment.Moment , args?:any[]): string {
    let rv = moment(value).format(args[0])
    return rv;
  }
}

@Pipe({
  name: 'tempsrelatif',
  pure: false,
})
@Injectable()
export class tempsRelatif implements PipeTransform {
  /*
    
   */
  transform(value:Date | moment.Moment): string {
    let rv = moment(value).fromNow();
    return rv;
  }
}



