
import { Component } from '@angular/core';
import { NavController, NavParams, ModalController } from 'ionic-angular';

import { Articles } from './../../providers/articles';
import { ArticlesUtilisateur } from './../../providers/articles-utilisateur';
import { ArticleComponent } from './../../components/article/article';


@Component({
  selector: 'page-actualites',
  templateUrl: 'actualites.html'
})
export class ActualitesPage {
  authentifie;
  actualite;
  affiche;
  articles;
  articlesRecents;
  articlesLus;
  articlesUtilisateur;
  categories: string = '';
  articlesFavoris;
  recherche;
  categorie;
  articlesFiltres;
  longueur;
  messageNombreRecherche;
    constructor(public navCtrl: NavController, public navParams: NavParams, public modalCtrl: ModalController,  public  article: Articles, public artUser: ArticlesUtilisateur) {
      this.actualite = "recent";
      this.recherche = "";
      this.categorie = "";
      this.authentifie = navParams.get('authentifie');
    }

  ionViewDidLoad() {

    if (this.authentifie === false) {
      this.rangerArticles();
    } else {
      this.chargerArticles();
    }

    console.log('ionViewDidLoad ActualitesPage');
  }

  ionViewDidEnter() {

  }


  afficheBarreRecherche(){
    if (this.affiche){
      this.affiche = false;
    }
    else this.affiche = true;
  }

  ouvrirArticle(article){
    let modal = this.modalCtrl.create(ArticleComponent, {
      article: article
    })
    modal.present();
  }

  chargerArticles () {
    this.article.obtenirArticle().then((data) => {

      this.articles = data;
      this.articlesRecents = this.articles.sort(
        function(a,b){
          a= new Date(a.datePost);
          b= new Date(b.datePost);
          return  a>b ? -1 : a<b ? 1 : 0;
        }
      )

      // Filtre d'articles

      this.articlesUtilisateur = this.articlesRecents.filter((item) => {
        var j=0; 
        var t=0;
        for(var i = 0; i < item.tags.length; i++) {
            if(item.tags[i].indexOf(this.recherche.toLowerCase()) > -1) {
                j= j+1
            }
        }
        for(var s = 0; s < item.tags.length; s++) {
            if(item.tags[s].toLowerCase().indexOf(this.categorie.toLowerCase()) > -1) {
                t= t+1
            }
        }
        
            return (
                t > 0 && (item.auteur.toLowerCase().indexOf(this.recherche.toLowerCase()) > -1 ||
                item.titre.toLowerCase().indexOf(this.recherche.toLowerCase()) > -1 ||
                j > 0 )
              );
        }); 

      
      // Articles lus et articles favoris

      this.artUser.obtenirDonnees().then((data) => {
        if ( typeof(data) != 'undefined' ) {
          this.articlesLus = data;
          
          // article lus
          for (var i in this.articlesLus) {
            for (var j in this.articlesUtilisateur) {
              if (this.articlesUtilisateur[j]._id==this.articlesLus[i]._id) {
                this.articlesUtilisateur[j].estLu = true;
                }
              }
            }

          // article favori
          this.articlesFavoris = this.articlesRecents;
          for (var i in this.articlesLus) {
            for (var j in this.articlesFavoris) {
              if (this.articlesFavoris[j]._id==this.articlesLus[i]._id && this.articlesLus[i].estFavori) {
                this.articlesFavoris[j].estFavori = true;
                }
              }
            };

          this.articlesFavoris = this.articlesFavoris.filter((item) => {
            return item.estFavori == true;
            });
          }
        
      });

      this.longueur = this.articlesUtilisateur.length
      
    })
  } 

  rangerArticles () {
    this.article.obtenirArticle().then((data) => {

      this.articles = data;
      this.articlesRecents = this.articles.sort(
        function(a,b){
          a= new Date(a.datePost);
          b= new Date(b.datePost);
          return  a>b ? -1 : a<b ? 1 : 0;
        }
      )

      this.articlesFiltres = this.articlesRecents.filter((item) => {
        var j=0;
        for(var i = 0; i < item.tags.length; i++) {
            if(item.tags[i].indexOf(this.recherche.toLowerCase()) > -1) {
                j= j+1
            }
        }
            return (
                item.auteur.toLowerCase().indexOf(this.recherche.toLowerCase()) > -1 ||
                item.titre.toLowerCase().indexOf(this.recherche.toLowerCase()) > -1 ||
                j > 0
              );
        }); 

        this.longueur = this.articlesFiltres.length
      
    });
  }

}


