
import { Component } from '@angular/core';
import { Http, Headers } from '@angular/http';
import { NavController } from 'ionic-angular';

import { NotificationsPage } from './../notifications/notifications';
import { ArticlesUtilisateur } from '../../providers/articles-utilisateur';


@Component({
  selector: 'page-inscription',
  templateUrl: 'inscription.html'
})
export class InscriptionPage {

  name: string;
  username: string;
  email: string;
  password: string;
  confirmPassword: string;
 
  constructor(public nav: NavController, public http: Http, public artUsers: ArticlesUtilisateur) {
 
  }
 
  enregistrer(){
 
      let headers = new Headers();
      headers.append('Content-Type', 'application/json');
 
      let user = {
        name: this.name,
        username: this.username,
        email: this.email,
        password: this.password,
        confirmPassword: this.confirmPassword
      };
 
      this.http.post('http://localhost:3000/auth/register', JSON.stringify(user), {headers: headers})
        .subscribe(res => {
          this.artUsers.init(res.json());
          this.nav.setRoot(NotificationsPage);
        }, (err) => {
          console.log(err);
        }); 
 
  }

}



