
import { Component, ViewChild } from '@angular/core';
import { Nav, Platform } from 'ionic-angular';
import { StatusBar, Splashscreen } from 'ionic-native';

import { InscriptionPage } from './../pages/inscription/inscription';
import { ConnexionPage } from './../pages/connexion/connexion';
import { ActualitesPage } from './../pages/actualites/actualites';
import { NotificationsPage } from './../pages/notifications/notifications';
import { FavorisPage } from './../pages/favoris/favoris';


@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  @ViewChild(Nav) nav: Nav;

  rootPage: any = ConnexionPage;

  pages: Array<{title: string, component: any}>;

  constructor(public platform: Platform) {
    this.initializeApp();

    // used for an example of ngFor and navigation
    this.pages = [
      { title: 'Connexion', component: ConnexionPage },
      { title: 'Inscription', component: InscriptionPage },
      { title: 'Actualités', component: ActualitesPage },
      { title: 'Favoris', component: FavorisPage },
      { title: 'Notifications', component: NotificationsPage }
    ];

  }

  initializeApp() {
    this.platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      StatusBar.styleDefault();
      Splashscreen.hide();
    });
  }

  openPage(page) {
    // Reset the content nav to have just this page
    // we wouldn't want the back button to show in this scenario
    this.nav.setRoot(page.component);
  }
}



