
import { Component, ViewChild } from '@angular/core';
import { ViewController, Slides, NavParams, ModalController } from 'ionic-angular';

import { Articles } from './../../providers/articles';
import { ArticlesUtilisateur } from './../../providers/articles-utilisateur';
import { DetailsCibleComponent } from './../details-cible/details-cible';

@Component({
    selector: 'article',
    templateUrl: 'article.html'
})
export class ArticleComponent {

    @ViewChild('mySlider') slider: Slides;
    sliderOptions: any;
    slideCount: number;
    article: any;
    favori: any;
    couleurFavori: any;
    Actif: any = "primary";
    Inactif: any = "";
    articleSelectionne;
    lambda;


    constructor(public viewCtrl: ViewController, public params: NavParams, public modalCtrl: ModalController, public  articles: Articles, public  artUser: ArticlesUtilisateur) {
        this.article = params.get('article');
        this.slideCount = 1;
        this.sliderOptions = {
            pager: false,
            autoplay: 0,
            loop: false,
            initialSlide: 0,
            speed: 300,
            direction: 'horizontal'
        }
        setTimeout(() => {
            this.slider.slideTo(1, 300);
        }, 750);

        this.artUser.obtenirDonnees().then((data) => {
            if (typeof(data) != 'undefined' ) { // en cas de connexion
                // fouille pour recupérer l'id de l'article savoir si l'article est favori; le cas echeant, k>0
                this.lambda = data;

                for (var i in this.lambda) {
                    if (this.lambda[i]._id == this.article._id ) {
                        this.articleSelectionne = this.lambda[i];
                    }
                }

                console.log(this.articleSelectionne)

                if (typeof this.articleSelectionne == 'undefined'){
                    this.couleurFavori = this.Inactif;
                } else {
                    if(this.articleSelectionne.estFavori == true) {
                        this.couleurFavori = this.Actif;
                    } else this.couleurFavori = this.Inactif;
                }
            }

        });
        
        
    }

    ionViewDidLoad() {
        console.log('ionViewDidLoad FavorisPage');
        //this.article.estLu = true;
        //this.favori = this.articleSelectionne.estFavori;
        // Inscription dans la liste des articles lus
        this.artUser.maj({
           _id: this.article._id//,
           //_rev: this.articleSelectionne._rev,
           //estLu: true,
           //estFavori: this.favori
        });

    }

    fermer() {
        

        // fermeture slider
        this.slider.slideTo(0, 300);
        setTimeout(() => {
            this.viewCtrl.dismiss();
        }, 750)
    }

    onSlideChanged(index) {
        this.slideCount = index + 1;
    }

    afficherDetails(cible) {
        let detailModal = this.modalCtrl.create(DetailsCibleComponent, {
      cible: cible, source: this.article.source
    });
    detailModal.present();
}

    choisirFavori(){
        
        this.artUser.obtenirDonnees().then((data) => {
            // fouille pour recupérer l'id de l'article savoir si l'article est favori; le cas echeant, k>0
            this.lambda = data;

            for (var i in this.lambda) {
                if (this.lambda[i]._id == this.article._id ) {
                    this.articleSelectionne = this.lambda[i];
                    
                }
            }

            // si l'article est favori, on change son statut de favori puis on met à jour le statut
            if (this.articleSelectionne.estFavori) {
                    this.articleSelectionne.estFavori = false;
                    this.couleurFavori = "";
                    //à completer
                    
                } else {
                    this.articleSelectionne.estFavori = true;
                    this.couleurFavori = "primary";
                    //à completer
                    
                };
           // Mise à jour du statut de favori
            this.artUser.maj({
                _id: this.article._id,
                _rev: this.articleSelectionne._rev,
                estFavori: this.articleSelectionne.estFavori
                });

            });
    }

}



