
import { Moment, tempsRelatif } from './../pipes/moment';

import { NgModule, ErrorHandler, LOCALE_ID } from '@angular/core';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { MyApp } from './app.component';
import { ConnexionPage } from './../pages/connexion/connexion';
import { InscriptionPage } from './../pages/inscription/inscription';
import { ActualitesPage } from './../pages/actualites/actualites';
import { ActualitesOfflinePage } from './../pages/actualites-offline/actualites-offline';
import { FavorisPage } from './../pages/favoris/favoris';
import { NotificationsPage } from './../pages/notifications/notifications';
import { ArticleComponent } from './../components/article/article';
import { DetailsCibleComponent } from './../components/details-cible/details-cible';
import { ArticlesUtilisateur } from './../providers/articles-utilisateur';
import { Articles } from './../providers/articles';


@NgModule({
  declarations: [
    MyApp,
    InscriptionPage,
    ConnexionPage,
    ActualitesPage,
    ActualitesOfflinePage,
    NotificationsPage,
    FavorisPage,
    ArticleComponent,
    DetailsCibleComponent,
    Moment, tempsRelatif
  ],
  imports: [
    IonicModule.forRoot(MyApp)
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    InscriptionPage,
    ConnexionPage,
    ActualitesPage,
    ActualitesOfflinePage,
    NotificationsPage,
    FavorisPage,
    ArticleComponent,
    DetailsCibleComponent
  ],
  providers: [
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    {provide: LOCALE_ID, useValue: 'fr'},
    Articles,
    ArticlesUtilisateur
    ]
})
export class AppModule {}



