
import { Component } from "@angular/core";
import { NavController, AlertController, ModalController } from 'ionic-angular';

import { ConnexionPage } from './../connexion/connexion';
import { ArticleComponent } from './../../components/article/article';
import { ArticlesUtilisateur } from './../../providers/articles-utilisateur';
import { Articles } from './../../providers/articles';
 
@Component({
  selector: 'page-notifications',
  templateUrl: 'notifications.html'
})
export class NotificationsPage {
 
  utilisateur: any;
  utilisateurs: any;
  articles: any;
  articlesRelatifs: any;
  articlesRelatifsRecents: any;
  articlesLus: any;
  articlesUtilisateur: any;
 
  constructor(public nav: NavController, public artUsers: ArticlesUtilisateur, public Arts: Articles, public alertCtrl: AlertController, public modalCtrl: ModalController) {
    
  }
 
  ionViewDidLoad(){

    this.utilisateur = this.artUsers.utilisateur;

    this.Arts.obtenirArticle().then((data) => {
      this.articles = data;
      this.articlesRelatifs = this.articles.filter((article) =>{
        var k=0; 
        for (var i in article.cible) {
          if (article.cible[i].matricule.toLowerCase() == this.utilisateur.user_id ) {
            k=k+1
          } 
        }
        return k>0
        
      });
      this.articlesRelatifsRecents = this.articlesRelatifs.sort(
        function(a,b){
          a= new Date(a.datePost);
          b= new Date(b.datePost);
          return  a>b ? -1 : a<b ? 1 : 0;
        }
      )

      this.articlesUtilisateur = this.articlesRelatifsRecents;

      this.artUsers.obtenirDonnees().then((data) => {
        this.articlesLus = data;

        for (var i in this.articlesLus) {
        for (var j in this.articlesUtilisateur) {
          if (this.articlesUtilisateur[j]._id==this.articlesLus[i]._id) {
            this.articlesUtilisateur[j].estLu = true;
          }
        }
      }
      });

    });
    
 
  }

  ouvrirArticle(article){
    let modal = this.modalCtrl.create(ArticleComponent, {
      article: article
    })
    modal.present();
  } 
 
  deconnexion(){
    this.artUsers.deconnexion();
    this.utilisateurs = null;
    this.nav.setRoot(ConnexionPage);
  }
 
  deleteTodo(todo){
    this.artUsers.supprimerArticleUtilisateur(todo);
  }
 
}


