import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import 'rxjs/add/operator/map';
import PouchDB from 'pouchdb';


@Injectable()
export class ArticlesUtilisateur {
 
  donneesUtilisateur: any;
  utilisateur: any;
  public bdUtilisateur: any;
  remoteUtilisateur: any;
 
  constructor(public http: Http) {
    console.log('Hello ArticlesUtilisateur Provider');
  }
 
  init(details){
    this.utilisateur = details;
 
    this.bdUtilisateur = new PouchDB('utilisateur');
 
    this.remoteUtilisateur = details.userDBs.supertest;
 
    let options = {
      live: true,
      retry: true,
      continuous: true
    };
 
    this.bdUtilisateur.sync(this.remoteUtilisateur, options);

 
  }
 
  deconnexion(){
 
    this.donneesUtilisateur = null;
 
    this.bdUtilisateur.destroy().then(() => {
      console.log("database removed");
    });
  }
 
  obtenirDonnees() {
 
    if (this.donneesUtilisateur) {
      return Promise.resolve(this.donneesUtilisateur);
    }
 
    return new Promise(resolve => {

      console.log(this.bdUtilisateur);
 
      if (this.bdUtilisateur) { //en cas de connexion
        this.bdUtilisateur.allDocs({
 
        include_docs: true
 
      }).then((result) => {
 
        this.donneesUtilisateur = [];
 
        let docs = result.rows.map((row) => {
          this.donneesUtilisateur.push(row.doc);
        });
 
        resolve(this.donneesUtilisateur);
 
        this.bdUtilisateur.changes({live: true, since: 'now', include_docs: true}).on('change', (change) => {
          this.handleChange(change);
        });
 
      }).catch((error) => {
 
        console.log(error);
 
      }); 
      }
 
    });
 
  }
 
  maj(article){
    console.log(this.bdUtilisateur)
    if (typeof(this.bdUtilisateur) != 'undefined') {
      this.bdUtilisateur.put(article).catch((err) => {
        console.log(err);
      });
    }
    
  }

 
  supprimerArticleUtilisateur(todo){
    this.bdUtilisateur.remove(todo).catch((err) => {
      console.log(err);
    });
  }
 
  handleChange(change){
 
    let changedDoc = null;
    let changedIndex = null;
 
    this.donneesUtilisateur.forEach((doc, index) => {
 
      if(doc._id === change.id){
        changedDoc = doc;
        changedIndex = index;
      }
 
    });
 
    //A document was deleted
    if(change.deleted){
      this.donneesUtilisateur.splice(changedIndex, 1);
    } 
    else {
 
      //A document was updated
      if(changedDoc){
        this.donneesUtilisateur[changedIndex] = change.doc;
      } 
 
      //A document was added
      else {
        this.donneesUtilisateur.push(change.doc); 
      }
 
    }
 
  }
 
}


